import React, { use, useEffect, useState } from "react";
import axios from "axios";

const StuffDuesSoon = () => {
  interface StuffDueItem {
    _id: string;
    title: string;
    assignedTo: string;
    dueDate: string;
    projectName: string;
    status: string; // Added status to handle the update
    projectId: string; // Added projectId to handle the project identification
  }

  const [stuffDue, setstuffDue] = useState<StuffDueItem[]>([]);
  const [error, setError] = useState<string | null>(null);
  const token = localStorage.getItem("token"); // Replace with the actual token

  useEffect(() => {
    const fetchMilestones = async () => {
      try {
        const response = await axios.get(
          `${process.env.NEXT_PUBLIC_API_URL}/projects/upcoming-milestones`, // Replace with the actual endpoint
          {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }
        );
        const milestones = response.data;
        // console.log(milestones)
        setstuffDue(milestones);
        // console.log(stuffDue);
      } catch (err) {
        if (axios.isAxiosError(err)) {
          setError(err.response?.data?.message || "Failed to fetch milestones");
        } else {
          setError("Failed to fetch milestones");
        }
      }
    };

    fetchMilestones();
  }, [token]);



  const handleStatusChange = async (milestoneId: string, projectId: string, newStatus: string) => {
    try {
      await axios.patch(
        `${process.env.NEXT_PUBLIC_API_URL}/projects/milestones/update-status/?projectId=${projectId}&milestoneId=${milestoneId}`,
        { status: newStatus }, // Request body
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );

      // Update status in the UI
      setstuffDue((prev) =>
        prev.map((item) =>
          item._id === milestoneId ? { ...item, status: newStatus } : item
        )
      );
    } catch (err) {
      console.error("Error updating milestone status:", err);
      setError("Failed to update milestone status. Please try again.");
    }
  };

  const colors = [
    "bg-yellow-100 text-yellow-600 border-yellow-400",
    "bg-red-100 text-red-600 border-red-400",
    "bg-green-100 text-green-600 border-green-400",
  ];

  const EmptyState = () => (
    <div className="flex flex-col items-center justify-center bg-white rounded-lg">
      <div className="mb-4">
        <div className="pt-8 mb-4">
          <img
            src="/assets/illustration (2).png"
            alt="No Project Found"
            className="w-48 h-30"
          />
        </div>
      </div>
      <h3 className="text-[#3d3d3d] text-sm font-sm mb-4">
        No Stuff Due Soon Found
      </h3>
    </div>
  );

  if (!stuffDue.length) {
    return <EmptyState />;
  }

  return (
    <div className="max-w-xl mx-auto bg-white p-6 rounded-md">
      {stuffDue.map((item, index) => (
        <div key={item._id}>
          <div className="flex items-center justify-between mb-4">
            <ul className="flex items-center space-x-2">
              <li className="flex items-center space-x-2">
                {/* Checkbox with onChange */}
                <input
                  type="checkbox"
                  className="w-4 h-4 text-blue-600 rounded"
                  checked={item.status === "completed"} // Reflect current status
                  onChange={() =>
                    handleStatusChange(
                      item._id,
                      item.projectId, // Replace with actual projectId if available
                      item.status === "completed" ? "pending" : "completed"
                    )
                  }
                />
                <div>
                  <h4 className="text-sm font-medium text-gray-600">
                    {item.title}
                  </h4>
                  <p className="text-xs text-gray-500">
                    {item.assignedTo} | Due date: {item.dueDate.slice(0, 10)}
                  </p>
                </div>
              </li>
            </ul>
            <div
              className={`px-2 py-1 text-xs rounded-lg ${
                colors[index % colors.length]
              }`}
            >
              {item.projectName}
            </div>
          </div>
          {/* Horizontal Line */}
          {index < stuffDue.length - 1 && (
            <hr className="border-t border-gray-300 my-4" />
          )}
        </div>
      ))}

      {error && <p className="text-red-600 text-sm mt-4">{error}</p>}
    </div>
  );
};

export default StuffDuesSoon;
