'use client';

import { useState, useEffect } from 'react';
import PinnedProjects from '@/components/dashboard/PinnedProjects';
import RecentTodos from '@/components/dashboard/RecentTodos';
import StuffDueSoon from '@/components/dashboard/StuffDueSoon';
import RecentChats from '@/components/dashboard/RecentChats';
import Schedule from '@/components/dashboard/Schedule';
import CreateNewProject from "@/components/dashboard/Create_Project_modal";
import InviteTeam from '@/components/dashboard/InviteTeam';
import Header from '../../../components/layout/Header'
import useSWR from 'swr';
import axios from 'axios';
import Toast, { toast } from '@/components/auth/Toast';
import { ChevronLeft, ChevronRight } from "lucide-react";
interface DashboardPageProps {
  userName: string;
}

export default function DashboardPage({ userName = "Esther Howard" }: DashboardPageProps) {
  const [isToast, setToast] = useState(false);

  const [isNewProjectOpen, setIsNewProjectOpen] = useState(false);
  const [isInvite, setisInvite] = useState(false);
  const [userNamee, setUserNamee] = useState("");
  const { mutate } = useSWR([`${process.env.NEXT_PUBLIC_API_URL}/projects/my-projects`, localStorage.getItem("token")]);
  const API_URL = process.env.NEXT_PUBLIC_API_URL;
  const handleProjectCreated = async () => {
    await mutate();
  }
  const openNewProject = () => {
    setIsNewProjectOpen(true);
  };

  const closeNewProject = () => {
    setIsNewProjectOpen(false);
  };

  const handleInvite = () => {
    setisInvite(true);
  };
  const closeInvite = () => {
    setisInvite(false);
  }

  const getGreeting = () => {
    const hour = new Date().getHours();
    if (hour < 12) return "Good Morning";
    if (hour < 18) return "Good Afternoon";
    return "Good Evening";

  };
  useEffect(() => {
    fetchUserId();
  }, []);
  const fetchUserId = async () => {
    try {
      const token = localStorage.getItem("token");
      const response = await axios.get(`${API_URL}/users/profile`, {
        headers: { Authorization: `Bearer ${token}` },
      });
      // console.log(response.data.fullName,"jkhiojhuio")
      setUserNamee(response.data.fullName);
    } catch (err) {
      console.error("Error fetching user ID:", err);

    }
  };

  return (
    <>
      <Toast />
      <div className="flex bg-gray-50 ">
        <div className="flex-1 flex flex-col bg-[#F8F7FA]">
          {/* <div className="mt-0.25 px-3 rounded-lg"> */}
            <Header />
          {/* </div> */}

          <div className="flex-1 px-6 grid-cols-7">
            {/* Dashboard Content */}

            <div className="col-span-2 flex justify-between items-center mb-8">
              <h1 className="text-sm font-semibold">
                {getGreeting()}, {userNamee}
              </h1>
              <div className="space-x-3">
                <button
                  className="text-sm px-6 py-0.5 bg-[#5D56BD] text-white rounded-lg hover:bg-[#4A4495]"
                  onClick={openNewProject}
                >
                  <span className='text-lg font-sm'>+</span> New Project
                </button>
                <button className="text-sm px-6 py-1 border border-[#D1D5DB] text-[#5D56BD] bg-[#E7E6F5] rounded-lg hover:bg-[#EFEFFB] transition-colors"
                  onClick={handleInvite}>
                  Invite Team
                </button>
              </div>
            </div>


            <div className="col-span-1 w-full pb-6">
              <div className="bg-white rounded-lg py-3 px-3 shadow-sm w-full">
                <div className="flex justify-between items-center mb-3">
                  <h2 className="text-medium font-medium">Pinned Projects</h2>
                  <div className="flex flex-row items-center">
                    <button className="flex items-center gap-1 text-[#5D56BD] text-xs hover:text-[#4A4495]">
                      Show all
                      <ChevronRight className="w-3 h-3" />
                    </button>
                  </div>

                </div>
                <PinnedProjects />
              </div>
            </div>



            <div className='grid grid-cols-5 gap-6'>
              {/* Recent To-dos Section */}
              <div className="bg-white rounded-lg p-6 py-3 px-3 shadow-sm col-span-2">
                <div className="flex justify-between items-center mb-4">
                  <h2 className="text-medium font-medium">Recent To-Do's</h2>
                  
                </div>
                <RecentTodos />
              </div>

              {/* Stuff Due Soon Section */}
              <div className="bg-white rounded-lg py-3 px-3 shadow-sm col-span-3">
                <div className="flex justify-between items-center mb-4">
                  <h2 className="text-medium font-medium">Stuff Due Soon</h2>
                </div>
                <StuffDueSoon />
              </div>

              {/* Recent Chats Section */}
              <div className="bg-white rounded-lg p-4 py-3 px-3 shadow-sm col-span-3 mb-10 h-[400px]">
  <div className="flex justify-between items-center mb-4">
    <h2 className="text-lg font-medium">Recent Chats</h2>
    <button className="flex items-center gap-1 text-[#5D56BD] text-xs hover:text-[#4A4495]">
      Show all
      <ChevronRight className="w-3 h-3" />
    </button>
  </div>
  <div className="h-[320px] overflow-y-auto">
    <RecentChats/>
  </div>
</div>
              {/* My Schedule Section */}
              <div className="bg-white rounded-lg py-3 px-3 shadow-sm col-span-2 mb-10">
                <div className="flex justify-between items-center mb-4">
                  <h2 className="text-lg font-medium">My Schedule</h2>
                  <button className="flex items-center gap-1 text-[#5D56BD] text-xs hover:text-[#4A4495]">
                      Show all
                      <ChevronRight className="w-3 h-3" />
                    </button>
                </div>
                <Schedule />
              </div>
            </div>
          </div>
        </div>


        {/* Create New Project Modal */}
        {isNewProjectOpen && <CreateNewProject onClose={closeNewProject} onProjectCreated={handleProjectCreated} />}
        {isInvite && <InviteTeam onClose={closeInvite} />}
      </div>
    </>
  );
}