import React, { useState, useEffect } from "react";
import axios from "axios";

const RecentToDos = () => {
  const [error, setError] = useState<string | null>(null);
  const [todos, setTodos] = useState<any[]>([]);
  const [isUpdating, setIsUpdating] = useState<boolean>(false); // Track API updates
  const token = localStorage.getItem("token");

  useEffect(() => {
    const fetchTodos = async () => {
      try {
        const response = await axios.get(
          `${process.env.NEXT_PUBLIC_API_URL}/todos`, // Replace with the actual endpoint
          {
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }
        );
        const fetchedTodos = response.data;
        // console.log(response.data);

        // Map backend data to match the required frontend structure
        const transformedTodos = fetchedTodos.map((todo: any) => ({
          _id: todo._id,
          projectId: todo.projectId,
          projectName: todo.projectId.name,
          tasks: [
            {
              id: todo._id,
              text: todo.task,
              completed: todo.status === "Completed",
            },
          ],
        }));

        setTodos(transformedTodos);
      } catch (err) {
        if (axios.isAxiosError(err)) {
          setError(err.response?.data?.message || "Failed to fetch To-Do's");
        } else {
          setError("Failed to fetch To-Do's");
        }
      }
    };

    fetchTodos();
  }, []);

  const colors = [
    "bg-yellow-100 text-yellow-600 border-yellow-400",
    "bg-red-100 text-red-600 border-red-400",
    "bg-green-100 text-green-600 border-green-400",
  ];

  const toggleCheckbox = async (projectId: string, taskId: string) => {
    try {
      setIsUpdating(true);

      // Find the current todo and task
      const todo = todos.find((t) => t._id === projectId);
      const task = todo.tasks.find((t : any) => t.id === taskId);

      // Send update to backend
      await axios.patch(
        `${process.env.NEXT_PUBLIC_API_URL}/todos/${taskId}`, // Replace with actual endpoint
        {
          status: task.completed ? "Pending" : "Completed", // Toggle status
        },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );

      // Update local state only after a successful backend response
      setTodos((prevTodos) =>
        prevTodos.map((todo) =>
          todo._id === projectId
            ? {
                ...todo,
                tasks: todo.tasks.map((task: any) =>
                  task.id === taskId
                    ? {
                        ...task,
                        completed: !task.completed,
                      }
                    : task
                ),
              }
            : todo
        )
      );

      setIsUpdating(false);
    } catch (err) {
      setIsUpdating(false);
      if (axios.isAxiosError(err)) {
        setError(err.response?.data?.message || "Failed to update status");
      } else {
        setError("Failed to update status");
      }
    }
  };

  const EmptyState = () => (
    <div className="flex flex-col items-center justify-center bg-white rounded-lg">
      <div className="mb-4">
        <div className=" rounded-full pt-20 mb-4">
          <img
            src="/assets/illustration (1).png"
            alt="No Project Found"
            className="w-48 h-36"
          />
        </div>
      </div>
      <h3 className="text-[#4D4D4D] text-sm font-sm mb-4">
        Empty To-Dos
      </h3>
    </div>
  );

  if (!todos.length) {
    return <EmptyState />;
  }

  return (
    <div className="p-4 bg-white rounded-lg">
      <h2 className="text-base font-semibold mb-4">Recent To-Do's</h2>
      {error ? (
        <p className="text-red-500 text-sm">{error}</p>
      ) : (
        <div className="space-y-4">
          {todos.map((todo, index) => (
            <div key={todo._id}>
              <h3
                className={`font-medium text-sm text-gray-900 mb-2 ${
                  colors[index % colors.length]
                } 
              pl-4 border-l-4 rounded-sm`}
              >
                Project: {todo.projectName}
              </h3>
              <ul className="space-y-2">
                {todo.tasks.map((task: any) => (
                  <li
                    key={task.id}
                    className="flex items-center text-sm text-gray-600"
                  >
                    <input
                      type="checkbox"
                      checked={task.completed}
                      onChange={() => toggleCheckbox(todo._id, task.id)}
                      className="mr-2 h-4 w-4 rounded border-gray-300"
                      disabled={isUpdating} // Prevent toggling during an update
                    />
                    {task.text}
                  </li>
                ))}
              </ul>
              <div className="border-t border-gray-300"></div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

export default RecentToDos;
