import React, { useEffect, useState } from "react";
import axios from "axios";
import Image from "next/image";

interface Project {
  _id: string;
  name: string;
  description: string;
}

const PinnedProjects = () => {
  const [pinnedProjects, setPinnedProjects] = useState<Project[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchPinnedProjects = async () => {
      try {
        const token = localStorage.getItem("token");
        const response = await axios.get(`${process.env.NEXT_PUBLIC_API_URL}/projects/pinned`, {
          headers: { Authorization: `Bearer ${token}` },
        });
        setPinnedProjects(response.data.projects || []);
      } catch (err) {
        console.error("Error fetching pinned projects:", err);
        setError("Failed to load pinned projects.");
      } finally {
        setLoading(false);
      }
    };

    fetchPinnedProjects();
  }, []);

  if (loading) return <p className="text-sm">Loading pinned projects...</p>;
  if (error) return <p className="text-sm text-red-500">{error}</p>;

  const EmptyState = () => (
    <div className="flex items-center justify-center py-4 bg-white rounded-lg">
      <div className="text-center">
        <div className=" p-4 mb-2 inline-block">
          <img src="/assets/illustration.png" alt="No Project Found" className="w-34 h-38" />
        </div>
        <h3 className="text-gray-700 text-sm">No Pinned Projects</h3>
      </div>
    </div>
  );

  if (!pinnedProjects.length) {
    return <EmptyState />;
  }

  return (
    <div className="w-full">
      <div className="flex space-x-4 overflow-x-auto">
        {pinnedProjects.map((project) => (
          <div
            key={project._id}
            className="bg-white rounded-lg p-3 w-64 border border-gray-200 hover:shadow-sm transition-shadow relative flex flex-col"
          >
            <div className="absolute top-2 right-2">
              <Image src="/assets/Pin.png" alt="Pin Icon" width={16} height={16} />
            </div>
            <h3 className="text-sm font-medium text-gray-800 mb-1 pr-6">{project.name}</h3>
            <p className="text-xs text-gray-600 line-clamp-2 mb-2">{project.description}</p>
            <div className="mt-auto">
              <Image
                src="/assets/avtar.jpg"
                alt={`${project.name} Profile`}
                width={24}
                height={24}
                className="rounded-full border border-gray-200"
              />
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default PinnedProjects;